@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");


.Header {
  position: fixed;
  left: 0px;
  top: -100px;
  width: 100%;
  height: 75px;
  background: white;
  z-index: 50;
  text-decoration: none;
  transition: top 0.6s;
  transition-delay: .5s;
}

.HeaderLogo {
  position: absolute;
  left: 50%;
  top: 66%;
  transform: translate(-50%, -50%);
  height: 100px;
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  width: 130px;
}

.Header:before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -76px;
  transform: translate(-50%, -50%);
  width: 230px;
  height: 85px;
  clip-path: polygon(50% 100%, 0 40%, 100% 40%);
  background: white;
}

.HeaderLogo img {
  margin: 0px;
  height: 100%;
  width: 100%;
}

.moviesicon {
  position: absolute;
  right: 10px;
  top: 52%;
  transform: translate(-50%, -50%);
  background: transparent;
  padding: 0px;
  display: none;
}

.moviesicon i {
  font-size: 30px;
  color: #111111;
  margin: auto;
}


.nav1 {
  position: absolute;
  left: 30%;
  top: 40%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 40px;
  background: transparent;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.nav1 a {
  font-size: 20px;
  color: #111111;
  font-family: 'Poppins';
  padding: 10px 20px;
  font-weight: 300;
  margin: auto;
  position: relative;
  background: transparent;
  border-radius: 0px;
  text-transform: uppercase;
  letter-spacing: 1px;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  transition: .4s;
}

.nav1 a:hover {
  color: black;
  text-shadow: 2px 2px 1px #31ff66;
}

.nav1 a:hover .navanim span:nth-child(1) {
  opacity: 1;
}

.nav1 a:hover .navanim span:nth-child(2) {
  height: 100%;
}

.nav1 a:hover .navanim span:nth-child(3) {
  margin-top: 0px;
}

.nav1 a:hover .navanim span:nth-child(4) {
  opacity: 1;
}

