.cartIcon {
  position: absolute;
  right: 10px;
  top: 52%;
  transform: translate(-50%, -50%);
  background: transparent;
  padding: 0px;
  display: none;
}

.cartIcon i {
  font-size: 30px;
  color: #111111;
  margin: auto;
}



.menuconcp {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
}

.menu-btn {
  width: 30px;
  height: 20px;
  cursor: pointer;
  /* Comment this when use on Tablets/Smartphones */
}

.menu-btn span {
  position: absolute;
  background: #111111;
  width: 100%;
  height: 15%;
  transition: .4s .4s top, .4s .4s bottom, .3s transform;
  border-radius: 100px;
}

.menu-btn span:nth-of-type(1) {
  top: 0;
}

.menu-btn span:nth-of-type(2) {
  top: 40%;
  opacity: 1;
  transition: opacity .1s .4s;
}

.menu-btn span:nth-of-type(3) {
  bottom: 0;
}

/* Animation */
.close span {
  transition: .4s top, .4s bottom, .4s .4s transform;
}

.close span:nth-of-type(1) {
  top: 40%;
  transform: rotate(45deg);
  background: #31ff66;
  opacity: 1;
}

.close span:nth-of-type(2) {
  opacity: 0;
  transition: opacity .1s .4s;
  background: #31ff66;
}

.close span:nth-of-type(3) {
  bottom: 40%;
  transform: rotate(-45deg);
  background: #31ff66;
  opacity: 1;
}

.center {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}


.menuconleft {
  position: fixed;
  right: 0px;
  top: 0px;
  width: 0%;
  opacity: 0;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(6px);
  display: none;
  z-index: 50;
  overflow: hidden;
  transition: 1s;
}

.menulist {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  background: transparent;
}

.menulist div {
  margin: 10px;
  width: 150px;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid #31ff66;
  box-shadow: 0px 0px 5px #31ff66;
  text-align: center;
  background-color: #111111;
  color: white;
}

.openmenu {
  width: 100%;
  opacity: 1;
}




