/*SMALLPHONE*/
@media (max-width: 600px) {

  .menuconleft {
    display: block;
  }

  .cartIcon {
    display: block;
  }

  .menuconcp {
    display: block;
  }

  .nav1 {
    display: none;
  }

  .nav2 {
    display: none;
  }
}